@import "./_variables";
@import "./_mixins";

.main {
    min-height: pc(1730);

    @include breakpoint-down('tablet') {
        min-height: vw(1730);
    }
}

.card {
    &-wrap {
        height: 100%;
        max-height: pc(729*2);
    }

    @include breakpoint-down('tablet') {
        &-wrap {
            max-height: vw(729*2);
        }
    }
}

.complete {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: pc(90) 0;
    text-align: center;

    &-tit {
        flex: 0 0 auto;
        padding-left: em(4, 74);
        font-size: pc(74);
        line-height: 1.3;
        font-weight: 300;
        letter-spacing: em(4, 74);
    }

    &-txt {
        flex: 1 0 auto;
        margin-top: pc(50);
        padding-left: em(4, 40);
        font-size: pc(48);
        line-height: 1.38;
        letter-spacing: em(4, 40);
    }

    &-pic {
        margin-top: pc(60);

        img {
            width: pc(760);
        }
    }

    &-action {
        flex: 0 0 auto;
    }

    @include breakpoint-down('tablet') {
        padding: vw(90) 0;

        &-tit {
            font-size: vw(74);
        }

        &-txt {
            margin-top: vw(50);
            font-size: vw(48);
        }

        &-pic {
            margin-top: vw(60);

            img {
                width: vw(760);
            }
        }
    }
}